*{
    padding: 0;
    margin: 0;
}

body{
    background-color: #ecf0f1;
}

section{
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.canvas canvas{
    position: absolute;
    top: 0;
    left: 0;

    opacity: 0;
    width: 1280px;
    height: 960px;
}

.canvas, .graph{
    padding: 10px;

    overflow: hidden;

    background-color: #161719;
    background-size: cover;

    min-width: calc(1280px/2);
    min-height: calc(960px/2);

    border-radius: 5px;

    -webkit-box-shadow: 0px 0px 20px 10px rgba(0,0,0,0.3); 
    box-shadow: 0px 0px 20px 10px rgba(0,0,0,0.3);

    position: relative;

    margin: 20px;

    transition: all, ease-out, 0.18s;

    cursor: pointer;
}

.graph{
    min-height: calc(960px/3);
}

.canvas:hover, .graph:hover{
    /* -webkit-box-shadow: 0px 0px 15px 2px rgba(0,0,0,0.59); 
    box-shadow: 0px 0px 15px 2px rgba(0,0,0,0.59); */
}

.canvas-input{
    opacity: 0;
    top: -100px;
}

.canvas-add-image{
    color: #fff;
    font-weight: lighter;
    cursor: pointer;

    bottom: 50%;
    left: 50%;
    transform: translate(-50%, 50%);
    
    position: absolute;
}

.equa{
    color: #fff;
    font-weight: lighter;
    font-size: 18px;

    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}